<template>
  <div class="region">
    <div class="buyheader">
      <h1>地区销售</h1>
    </div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/bell' }">产品列表</el-breadcrumb-item>
      <el-breadcrumb-item>地区销售</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="center-bell">
      <ve-heatmap :data="chartData" :settings="chartSettings"></ve-heatmap>
    </div>

  </div>
</template>

<script>
export default {
  data() {
    return {
    };
  },
  created: function () {
      this.chartData = {
        columns: ['lat', 'lng', '人数'],
        rows: [
          { 'lat': 115.892151, 'lng': 28.676493, '人数': 1000 },
          { 'lat': 117.000923, 'lng': 36.675807, '人数': 400 },
          { 'lat': 113.665412, 'lng': 34.757975, '人数': 800 },
          { 'lat': 114.298572, 'lng': 30.584355, '人数': 200 },
          { 'lat': 112.982279, 'lng': 28.19409, '人数': 100 },
          { 'lat': 113.280637, 'lng': 23.125178, '人数': 300 },
          { 'lat': 110.33119, 'lng': 20.031971, '人数': 800 },
          { 'lat': 104.065735, 'lng': 30.659462, '人数': 700 },
          { 'lat': 108.948024, 'lng': 34.263161, '人数': 300 },
          { 'lat': 103.823557, 'lng': 36.058039, '人数': 500 }
        ]
      }
      this.chartSettings = {
        position: 'china',
        type: 'map',
        geo: {
          label: {
            emphasis: {
              show: false
            }
          },
          itemStyle: {
            normal: {
              areaColor: '#461b93',
              borderColor: '#461b93'
            },
            emphasis: {
              areaColor: '#2a333d'
            }
          }
        }
      }
    }
};
</script>

<style lang="scss" scoped>
.center-bell{
  padding: 15px;
  a:last-child{
    margin-bottom: 0;
  }
  a{
    background: #eee;
    display: block;
    padding: 10px;
    margin: 10px 0;
    color: #461b93;
  }
}


</style>



